import React , { useState , useEffect , useContext } from "react";
import ReactMapGL , { NavigationControl , Marker , Popup  } from 'react-map-gl'
import {unstable_useMediaQuery as useMediaQuery} from '@material-ui/core/useMediaQuery';
import { withStyles } from "@material-ui/core/styles";
import Context from '../context'
import PinIcon from './PinIcon'
import Blog from './Blog'
import { useClient } from '../client'
import { GET_PINS_QUERY } from '../graphql/queries'
import { DELETE_PIN_MUTATION } from '../graphql/mutations'
import differenceInMinutes from 'date-fns/difference_in_minutes'
import { Typography } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import DeleteIcon from "@material-ui/icons/DeleteTwoTone";


const accessToken = "pk.eyJ1Ijoic3NhYXZ2YWEiLCJhIjoiY2p6cjF5bnhkMTJ3NDNsbnAzMmQ3b2o5MSJ9.mqQ7_qclr3C1RJzbz-c2ww"
const initialViewport = {
  latitude: 37.7577,
  longitude: -122.4376,
  zoom: 13
}

const Map = ({ classes }) => {
 const client = useClient()
 const   mobileSize = useMediaQuery('(max-width: 650px)')
 const { state , dispatch } = useContext(Context)
 const [ viewPort , setViewPort ] = useState(initialViewport)
 const [ userPosition , setUserPosition ] =  useState(null)
 const [ popup , setPopup ] = useState(null)

 useEffect(() => {
   const pinExist = popup && state.pins.findIndex(pin => pin._id === popup._id) > -1
   if(!pinExist){
     setPopup(null)
   }
 } , [state.pins.length])

 useEffect(() => {
  getPins()
 },[])

 useEffect(() => {
  getUserPosition()
 },[])

 const getUserPosition = () => {
   if('geolocation' in navigator){
     navigator.geolocation.getCurrentPosition(position => {
      const { latitude , longitude } = position.coords
      setViewPort({...viewPort , latitude , longitude })
      setUserPosition({ latitude , longitude })
     })
   }
 }



 const getPins = async() => {
  const { getPins } = await client.request(GET_PINS_QUERY)
  dispatch({ type:"GET_PINS" , payload:getPins })
 }

 const handleMapClick = ({lngLat , leftButton }) => {
   if(!leftButton) return 
   if(!state.draft){
     dispatch({ type: "CREATE_DRAFT" })
   }

   const [ longitude , latitude ] = lngLat

    dispatch({
      type: "UPDATE_DRAFT_LOCATION",
      payload:{
        longitude,
        latitude
      } })

 }

 const highLightNewPin = pin => {
  const isNewPin =  differenceInMinutes(Date.now() , Number(pin.createdAt)) <= 30
  return isNewPin ? 'limegreen' : "darkblue";
 }

 const handleSelectPin = pin => {
   setPopup(pin)
   dispatch({ type: "SET_PIN" , payload: pin })
 }

 const handleDeletePin = async pin => {
   const variables = { pinId:pin._id }
   await client.request(DELETE_PIN_MUTATION , variables )
   setPopup(null)
 }

 const isAuthUser = () => state.currentUser._id === popup.author._id

  return (
    <div className = { mobileSize ? classes.rootMobile : classes.root} >
      <ReactMapGL
      width = "100vw"
      height = "calc(100vh - 64px)"
      mapStyle = "mapbox://styles/mapbox/basic-v9"
      mapboxApiAccessToken = {accessToken}
      onClick = {handleMapClick}
      onViewportChange = { viewport => setViewPort(viewport)}
      {...viewPort}
      >
        <div className = {classes.navigationControl}>
       <NavigationControl
          onViewportChange = { viewport => setViewPort(viewport)}
       />
       </div>
       {userPosition && (
        <Marker
           latitude = {userPosition.latitude}
           longitude = {userPosition.longitude}
          >
            <PinIcon size = {40} color = "red" />
            <p style={{position:"relative" , background:"black", color:"white", borderRadius:"4px" , padding:"3px 7px", top:-20}}>you here</p>
        </Marker>
       )}
       {state.draft && (
         <Marker
         latitude = {state.draft.latitude}
         longitude = {state.draft.longitude}
        >
          <PinIcon size = {40} color = "rgba(0,0,0,0.5)" />
      </Marker>
       )}

       { state.pins.map(pin => 
        <Marker
        key = {pin._id}
        latitude = {pin.latitude}
        longitude = {pin.longitude}
       >
         <PinIcon onClick = {() => handleSelectPin(pin)} size = {40} color = {highLightNewPin(pin)} />
     </Marker>
     )}

     {popup && (
       <Popup
       anchor = "top"
       latitude = {popup.latitude}
       longitude = {popup.longitude}
       closeOnClick = {false}
       onClose = {() => setPopup(null)}
       >
         <img 
         className = {classes.popupImage}
         src={popup.image}
         alt={popup.title}
         />
         <div className = {classes.popupTab}>
           <Typography>
             {popup.latitude.toFixed(6)} , {popup.longitude.toFixed(6)}

           </Typography>
           {isAuthUser() && (
             <Button onClick = {() => handleDeletePin(popup) }>
               <DeleteIcon className = {classes.deleteIcon} />
             </Button>
           )}
         </div>
       </Popup>
     )}
      </ReactMapGL>
  
      <Blog />
    </div>
  )
};

const styles = {
  root: {
    display: "flex"
  },
  rootMobile: {
    display: "flex",
    flexDirection: "column-reverse"
  },
  navigationControl: {
    position: "absolute",
    top: 0,
    left: 0,
    margin: "1em"
  },
  deleteIcon: {
    color: "red"
  },
  popupImage: {
    padding: "0.4em",
    height: 200,
    width: 200,
    objectFit: "cover"
  },
  popupTab: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  }
};

export default withStyles(styles)(Map);
