 import React , { useState , useContext } from "react";
 import {unstable_useMediaQuery as useMediaQuery} from '@material-ui/core/useMediaQuery';
 import axios from 'axios'
 import { withStyles } from "@material-ui/core/styles";
 import TextField from "@material-ui/core/TextField";
 import Typography from "@material-ui/core/Typography";
 import Button from "@material-ui/core/Button";
 import AddAPhotoIcon from "@material-ui/icons/AddAPhotoTwoTone";
 import LandscapeIcon from "@material-ui/icons/LandscapeOutlined";
 import ClearIcon from "@material-ui/icons/Clear";
 import SaveIcon from "@material-ui/icons/SaveTwoTone";
 import Context from '../../context'
 import { CREATE_PIN_MUTATION } from '../../graphql/mutations'
 import { useClient } from '../../client'

 const CreatePin = ({ classes }) => {
  const mobileSize = useMediaQuery('(max-width: 650px)')
  const client = useClient()
  const { state , dispatch } = useContext(Context)
  const [ title , setTitle ] = useState('')
  const [ image , setImage ] = useState('')
  const [ content , setContent ] = useState('')
  const [ submitting , setSubmitting ] = useState(false)

const handleSubmit = async(event) => {
  try {
    event.preventDefault()
    setSubmitting(true)
    const url = await handleImageUpload()

    const { latitude , longitude } = state.draft

    const variables = {
      title,
      image: url,
      content,
      latitude,
      longitude
    }

    await client.request(CREATE_PIN_MUTATION , variables)
    handleDiscard()

  } catch (error) {
    setSubmitting(false)

  }

}

const handleDiscard = () => {
  setTitle('')
  setImage('')
  setContent('')
  dispatch({type:'DISCARD_DRAFT'})
}

const handleImageUpload = async() => {
 const data = new FormData()
 data.append('file' , image)
 data.append('upload_preset' , 'geopins')
 data.append('cloud_name' , 'djke3wknc')
 const res = await axios.post(
   'https://api.cloudinary.com/v1_1/djke3wknc/image/upload',
    data
 )
 return res.data.url
}

  return (
    <form className = {classes.form}>
       <Typography 
        className = {classes.alignCenter}
        component = 'h2'
        style={{marginBottom:20}}
        variant = 'h4'
        color = 'secondary'
        >
         <LandscapeIcon className = {classes.iconLarge} />  pin location
       </Typography>
       <div style = {{display:"flex" , flexDirection:"column" , justifyContent:"center" , alignContent:"center"}} className = {classes.contentField}>
         <TextField
         onChange = {e => setTitle(e.target.value)}
          name = "title"
          style={{display:"flex" , width:"100%", marginBottom:"20px"}}
          label = "title"
          placeholder = "Insert pin title"
         />
          <label
          style={{display:"flex" ,
                  marginBottom:"20px" ,
                  color: image && "green",
                  justifyContent:"center" ,
                  alignContent:"center"}}
          htmlFor = "image">
              <AddAPhotoIcon />
          </label>
         <input 
            placeholder = "image"
            style = {{ display:"block" , marginBottom:"20px"}}
            accept = "image/*"
            type="file"
            onChange = {e => setImage(e.target.files[0])}
            id="image" />
       </div>
        <div className = {classes.contentField}>
          <TextField 
           onChange = {e => setContent(e.target.value)}
           name = 'content'
           label = 'Content'
           multiline
           rows = { mobileSize ? '3' : '6' }
           margin = "normal"
           fullWidth
           variant = 'outlined'
           />
        </div>
        <div>
          <Button
          onClick = {handleDiscard}
           className = {classes.button}
           variant = 'contained'
           color = 'primary'
          >
            <ClearIcon
             className = {classes.leftIcon}
            />
            Discard
          </Button>
          <Button
           type = 'submit'
           disabled = {!title.trim() || !content.trim() || !image || submitting}
           onClick = {handleSubmit}
           className = {classes.button}
           variant = 'contained'
           color = 'secondary'
          >
            Submit
            <SaveIcon
             className = {classes.rightIcon}
            />
          </Button>
        </div>
    </form>
  )
};

const styles = theme => ({
  form: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    paddingBottom: theme.spacing.unit
  },
  contentField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: "95%"
  },
  input: {
    display: "none"
  },
  alignCenter: {
    display: "flex",
    alignItems: "center"
  },
  iconLarge: {
    fontSize: 40,
    marginRight: theme.spacing.unit
  },
  leftIcon: {
    fontSize: 20,
    marginRight: theme.spacing.unit
  },
  rightIcon: {
    fontSize: 20,
    marginLeft: theme.spacing.unit
  },
  button: {
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
    marginRight: theme.spacing.unit,
    marginLeft: 0
  }
});

export default withStyles(styles)(CreatePin);
